.img{
    height: 40vh;
    width: 31vh;
}

.genreoption{
    cursor: pointer;
    padding: 1em;
    color: var(--g);
    margin: .3em;
    transition: .1s;
    position: relative;
    
}

.caption{
    font-size: 39px;
    position: absolute;
    bottom: -1em;
    left: -0.1em;
    width: 100%;
    background-color: rgba(255, 255, 255, 0);
    text-align: center;
    opacity: 0;
    transition: 0.2s ease-in-out;
}

.genreoption:hover .caption{
    opacity: 1;
    bottom: 0;
}

.genres{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 10vh;
}
.main{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.head{
    font-size: 60px;
    text-align: center;
    padding: 2em;
}